<template>
    <div class='grade-table'>
         <el-table
                max-height="800"
                :data="tableData"
                stripe
                
                style="width: 100%">
                <el-table-column
                sortable
                prop="semester"
                label="Semester"
                :filters="[{text:'Semester1-1',value:'Semester1-1'},
                           {text:'Semester1-2',value:'Semester1-2'},
                           {text:'Semester2-1',value:'Semester2-1'},
                           {text:'Semester2-1',value:'Semester2-2'},
                           {text:'Semester3-1',value:'Semester3-1'},
                           {text:'Semester3-2',value:'Semester3-2'},                
                ]"
                :filter-method="filterHandler"
                width="200">
                </el-table-column>
                <el-table-column
                prop="name"
                label="Class"
                width="180">
                </el-table-column>
                <el-table-column
                prop="credit"
                label="Credit"
                width="180">
                </el-table-column>
                <el-table-column
                prop="grade"
                label="Grade"
                width="180">
                </el-table-column>
                <el-table-column
                prop="keywords"
                label="Keywords"
                :filters="[{text:'Technology',value:'Technology'},
                           {text:'Science',value:'Science'},
                           {text:'Liberal Arts',value:'Liberal Arts'},               
                ]"
                :filter-method="filterHandler"
                >
                    <template slot-scope="scope">
                        <el-tag
                        v-if="scope.row.keywords==='Technology'"
                        type="danger"
                        disable-transitions>{{scope.row.keywords}}</el-tag>

                        <el-tag
                        v-if="scope.row.keywords==='Science'"
                        type="warning"
                        disable-transitions>&nbsp;&nbsp;{{scope.row.keywords}}&nbsp;&nbsp;&nbsp;</el-tag>


                        <el-tag
                        v-if="scope.row.keywords==='Liberal Arts'"
                        type="success"
                        disable-transitions>{{scope.row.keywords}}</el-tag>
                    </template>

                </el-table-column>
            </el-table>
    </div>
</template>

<script>
export default {
    data() {
      return {
        tableData: [
            { semester: 'Semester1-1',  name: '', grade: '95', credit:'2', keywords:'Technology', },
            { semester: 'Semester1-1',  name: '', grade: '89', credit:'2', keywords:'Technology', },
            { semester: 'Semester1-1',  name: '', grade: '93', credit:'5', keywords:'Science', },
            { semester: 'Semester1-1',  name: '', grade: '99', credit:'3', keywords:'Science', },
            { semester: 'Semester1-1',  name: '1-2', grade: '88', credit:'6', keywords:'Liberal Arts', },
            { semester: 'Semester1-1',  name: '1', grade: '95', credit:'0.4', keywords:'Liberal Arts', },
            { semester: 'Semester1-1',  name: '', grade: '94', credit:'1', keywords:'', },
            { semester: 'Semester1-1',  name: '', grade: '94', credit:'0.5', keywords:'Liberal Arts', },
            { semester: 'Semester1-1',  name: '', grade: '93', credit:'3', keywords:'Liberal Arts', },
            { semester: 'Semester1-1',  name: '', grade: '95', credit:'2', keywords:'Liberal Arts', },
            { semester: 'Semester1-1',  name: '', grade: '82', credit:'1', keywords:'', },
            { semester: 'Semester1-2',  name: '', grade: '89', credit:'3', keywords:'Technology', },
            { semester: 'Semester1-2',  name: 'C', grade: '95', credit:'3', keywords:'Technology', },
            { semester: 'Semester1-2',  name: '', grade: '92', credit:'1', keywords:'Technology', },
            { semester: 'Semester1-2',  name: '', grade: '100', credit:'5', keywords:'Science', },
            { semester: 'Semester1-2',  name: '2', grade: '98', credit:'0.4', keywords:'Liberal Arts', },
            { semester: 'Semester1-2',  name: '', grade: '100', credit:'1.5', keywords:'Liberal Arts', },
            { semester: 'Semester1-2',  name: '3', grade: '85', credit:'4', keywords:'Liberal Arts', },
            { semester: 'Semester1-2',  name: '', grade: '85', credit:'2', keywords:'Liberal Arts', },
            { semester: 'Semester1-2',  name: '', grade: '88', credit:'3', keywords:'Liberal Arts', },
            { semester: 'Semester1-2',  name: '', grade: '90', credit:'3', keywords:'Science', },
            { semester: 'Semester1-2',  name: '', grade: '84', credit:'1.5', keywords:'Science', },
            { semester: 'Semester1-2',  name: 'I', grade: '91', credit:'0.4', keywords:'Liberal Arts', },
            { semester: 'Semester2-1',  name: '', grade: '94', credit:'2', keywords:'Technology', },
            { semester: 'Semester2-1',  name: '', grade: '94', credit:'2', keywords:'Technology', },
            { semester: 'Semester2-1',  name: '', grade: '95', credit:'4', keywords:'Science', },
            { semester: 'Semester2-1',  name: '', grade: '99', credit:'2', keywords:'Science', },
            { semester: 'Semester2-1',  name: '', grade: '87', credit:'3', keywords:'Technology', },
            { semester: 'Semester2-1',  name: '', grade: '95', credit:'3', keywords:'Technology', },
            { semester: 'Semester2-1',  name: '', grade: '86', credit:'2.5', keywords:'Liberal Arts', },
            { semester: 'Semester2-1',  name: '3', grade: '92', credit:'0.4', keywords:'Liberal Arts', },
            { semester: 'Semester2-1',  name: '', grade: '80', credit:'1', keywords:'', },
            { semester: 'Semester2-2',  name: 'Java', grade: '95', credit:'4', keywords:'Technology', },
            { semester: 'Semester2-2',  name: '', grade: '99', credit:'3', keywords:'Science', },
            { semester: 'Semester2-2',  name: '', grade: '88', credit:'3', keywords:'Technology', },
            { semester: 'Semester2-2',  name: '', grade: '91', credit:'1', keywords:'Technology', },
            { semester: 'Semester2-2',  name: '1', grade: '87', credit:'2', keywords:'Liberal Arts', },
            { semester: 'Semester2-2',  name: '2', grade: '87', credit:'2', keywords:'Liberal Arts', },
            { semester: 'Semester2-2',  name: '', grade: '94', credit:'4', keywords:'Liberal Arts', },
            { semester: 'Semester2-2',  name: '', grade: '93', credit:'2', keywords:'Liberal Arts', },
            { semester: 'Semester2-2',  name: '', grade: '84', credit:'3', keywords:'Technology', },
            { semester: 'Semester2-2',  name: '', grade: '88', credit:'2.5', keywords:'Liberal Arts', },
            { semester: 'Semester2-2',  name: 'II', grade: '100', credit:'0.3', keywords:'Liberal Arts', },
            { semester: 'Semester2-2',  name: '', grade: '98', credit:'4', keywords:'Technology', },
            { semester: 'Semester3-1',  name: 'Design & Build 1	', grade: '91', credit:'2', keywords:'Technology', },
            { semester: 'Semester3-1',  name: '', grade: '95', credit:'2.5', keywords:'Technology', },
            { semester: 'Semester3-1',  name: '', grade: '94', credit:'3', keywords:'Technology', },
            { semester: 'Semester3-1',  name: '', grade: '92', credit:'3', keywords:'Technology', },
            { semester: 'Semester3-1',  name: '', grade: '91', credit:'3', keywords:'Technology', },
            { semester: 'Semester3-1',  name: '', grade: '77', credit:'1', keywords:'', },
            { semester: 'Semester3-1',  name: '5', grade: '90', credit:'0.4', keywords:'Liberal Arts', },
            { semester: 'Semester3-2',  name: '', grade: '96', credit:'4', keywords:'Technology', },
            { semester: 'Semester3-2',  name: '', grade: '97', credit:'2', keywords:'Technology', },
            { semester: 'Semester3-2',  name: 'Web', grade: '96', credit:'2', keywords:'Technology', },
            { semester: 'Semester3-2',  name: '', grade: '97', credit:'3', keywords:'Technology', },
            { semester: 'Semester3-2',  name: '', grade: '96', credit:'3', keywords:'Technology', },
            { semester: 'Semester3-2',  name: '', grade: '96', credit:'3', keywords:'Technology', },
            { semester: 'Semester3-2',  name: '', grade: '92', credit:'4', keywords:'Technology', },
            { semester: 'Semester3-2',  name: 'III', grade: '96', credit:'0.3', keywords:'Liberal Arts', },
            
        ]
      }
    },
    methods:{
        filterHandler(value, row, column) {
            const property = column['property'];
            return row[property] === value;
        }
    }

}
</script>

<style>
.grade-table .el-icon-arrow-down:before {
    content: "\E6DF";
    font-weight: bold;
    font-size: 16px!important;
}
</style>


